@import "../../breakpoint.scss";
.navbar-container {
	width: 16%;
	@include media("<=tablet") {
		height: 100vh;
		display: none;
	}
	.navbar {
		font-size: 18px;
		width: 100%;
		overflow: hidden;
		z-index: 100;
		min-width: 320px;
		.little-nav {
			display: none;
		}
		transition: all 0.5s;
		margin-left: 40px;
		background: rgba(255, 255, 255, 0.25);
		box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
		backdrop-filter: blur(4px);
		-webkit-backdrop-filter: blur(4px);
		border-radius: 10px;
		border: 1px solid rgba(255, 255, 255, 0.18);
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: space-between;
		height: 95vh;
		border-radius: 30px;
		&::before {
			width: 100vw;
			height: 100vh;
			background-color: #7ddaeb63;
		}
		@include media("<=tablet") {
			margin-left: 0px;
			width: 300px;
			min-width: 150px;
			height: 100vh;
			position: absolute;
			left: 0;
			margin: auto;
		}
		.hb-line {
			margin-top: 40px;
			width: 80%;
			display: none;
			align-items: flex-end;
			flex-direction: column;
			cursor: pointer;
			@include media("<=tablet") {
				display: flex;
			}
			.nav-logo {
				width: 128px;
			}
			.line-container {
				display: flex;
				flex-direction: column;
				.line {
					height: 3px;
					width: 24px;
					border-radius: 2px;
					background: #7ddaeb63;
					margin-top: 5px;
					&:first-child {
						margin-right: 8px;
						width: 16px;
					}
					&:last-child {
						margin-right: 14px;
						width: 10px;
					}
				}
			}
		}
		.clicked {
			flex-direction: column-reverse !important;
			align-items: center !important;
			.line-container {
				margin-bottom: 20px;
			}
		}
		.hb-line2 {
			margin-top: 5px;
			width: 80%;
			display: flex;
			justify-content: space-between;
			cursor: pointer;
			@include media("<=tablet") {
				display: none;
			}
			.nav-logo {
				width: 128px;
			}
			.line-container {
				display: flex;
				flex-direction: column;
				.line {
					height: 3px;
					width: 24px;
					border-radius: 2px;
					background: #7ddaeb63;
					margin-top: 5px;
					&:first-child {
						margin-right: 8px;
						width: 16px;
					}
					&:last-child {
						margin-right: 14px;
						width: 10px;
					}
				}
			}
		}
		nav {
			color: #959595;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			height: 50%;
			margin-left: 10px;
			.nav-item {
				text-decoration: none;
				cursor: pointer;
				display: flex;
				align-items: center;
				padding-left: 20px;
				border-radius: 20px;
				color: #959595;
				span {
					margin-left: 30px;
					padding-right: 120px;
					padding-top: 20px;
					padding-bottom: 20px;
					text-decoration: none;
				}
				&:hover {
					color: white;
					border-radius: 20px;
				}
			}
			.dashboard {
				&::before {
					content: url("../../assets/dashboard.svg");
					margin-top: 4px;
				}
				&:hover {
					&::before {
						content: url("../../assets/dashboard-green.svg");
						fill: white;
					}
				}
			}
			.exchange {
				&::before {
					margin-top: 4px;
					content: url("../../assets/exchange.svg");
				}
				&:hover {
					&::before {
						content: url("../../assets/exchange-green.svg");
					}
				}
			}
			.liquidity {
				&::before {
					margin-top: 4px;
					content: url("../../assets/liquidity.svg");
				}
				&:hover {
					&::before {
						content: url("../../assets/liquidity-green.svg");
					}
				}
			}
			.pool {
				&::before {
					margin-top: 4px;
					content: url("../../assets/pool.svg");
				}
				&:hover {
					&::before {
						content: url("../../assets/pool-green.svg");
					}
				}
			}
			.farms {
				&::before {
					margin-top: 4px;
					content: url("../../assets/farm.svg");
				}
				&:hover {
					&::before {
						content: url("../../assets/farm-green.svg");
					}
				}
			}
			.win {
				&::before {
					margin-top: 4px;
					content: url("../../assets/win.svg");
				}
				&:hover {
					&::before {
						content: url("../../assets/win.svg");
					}
				}
			}
			// .lemon {
			//   &::before {
			//     margin-top: 4px;
			//     content: url("../../assets/marketPlace.svg");
			//   }
			//   &:hover {
			//     &::before {
			//       content: url("../../assets/lemonade-network.svg");
			//     }
			//   }
			// }
		}
		.find-us {
			display: flex;
			align-items: center;
			width: 70%;
			margin-top: 30px;
			margin-bottom: 30px;
			justify-content: space-between;
			a {
				transition: all ease 0.3s;
				&:hover {
					transform: translateY(-5px);
				}
			}
		}
	}
	.close {
		min-width: 200px;
		width: 20%;
		nav {
			.nav-item {
				padding-left: 0;
			}
		}
	}
}
